<template>
    <div class="card-parent shadow-sm">
        <div class="card-head w-100 mb-2">
            <div class="container-fluid">
                <div class="row">
                    <div class="col-12 color-type"></div>
                </div>
                <div class="row card-title text-center align-items-center text-white py-2 text-uppercase py-3">
                    <div class="col-12 p-0">
                        <p class="p-0 m-0"> <b>Appt-{{ appt }}</b> <sup>Bloc-{{ bloc }}</sup></p>
                    </div>
                </div>
            </div>
        </div>

        <div class="container-fluid">
            <div class="row">
                <div class="card-image d-flex align-items-center p-1">
                    <ImageComponent :src="`assets/images/lots/3d/${cardInfo.nom}.jpg`" />
                </div>
            </div>
        </div>

        <hr />

        <div class="container-fluid">

            <div class="card-content row align-items-center">
                <div class="col-5  content-title p-2">
                    <span class="">Etage</span>
                </div>
                <div class="col-7 content-value">
                    <span>{{ stage }}</span>
                </div>
            </div>

            <div class="card-content row align-items-center">
                <div class="col-5 content-title p-2">
                    <span class="">Type</span>
                </div>
                <div class="col-7 content-value">
                    <span>{{ cardInfo.type }}</span>
                </div>
            </div>

            <div class="card-content row align-items-center">
                <div class="col-5 content-title p-2">
                    <span class="">Surface</span>
                </div>
                <div class="col-7 content-value">
                    <span>{{ cardInfo.surface }} M<sup>2</sup></span>
                </div>
            </div>

            <div class="card-content row align-items-center">
                <div class="col-5 content-title p-2">
                    <span class="">Terrasse</span>
                </div>
                <div class="col-7 content-value">
                    <span>{{ cardInfo.terrasse }} M<sup>2</sup></span>
                </div>
            </div>

            <div class="card-content row align-items-center">
                <div class="col-5 content-title p-2">
                    <span class="">Code</span>
                </div>
                <div class="col-7 content-value">
                    <span>{{ cardInfo.code_ref }}</span>
                </div>
            </div>
        </div>

        <hr />

        <edit-appt :card-info="cardInfo" :color-type="typeColor" :is-admin="isAdmin"></edit-appt>
    </div>
</template>


<script setup>

import { ref, computed } from "vue";
import ImageComponent from "@/components/app-ui/ImageComponent";
import EditAppt from "@/components/admin/EditAppt";



const props = defineProps({
    cardInfo: Object,
    typesColors: Object,
    isAdmin: {
        type: Boolean,
        default: false
    }
});

const colorsProps = ref(props.typesColors);
const appt = computed(() => {
    const a = props.cardInfo.appt;
    const prefix = (a <= 9) ? "0" : "";
    return prefix + a;
});
const bloc = computed(() => {
    const a = props.cardInfo.bloc;
    const prefix = (a <= 9) ? "0" : "";
    return prefix + a;
});
const typeColor = computed(() => {
    const type = props.cardInfo.type;
    return colorsProps.value[type] || "#cecece";
});

const stage = computed(() => {
    const stage = props.cardInfo.etage;
    return stage == 0 ? "RDC" : stage;
});


</script>

<style lang="scss" scoped>
$type-color : v-bind(typeColor);

.card-parent {

    border: 1px solid #e6e6e6;
    overflow: hidden;
    border-radius: .2em;

    hr {
        color: var(--app-secondary-color);
    }

    .card-head {

        letter-spacing: .1em;
        font-size: .9em;

        .color-type {
            height: .5em;
            background-color: $type-color;
        }

        .card-title {
            background-color: var(--app-gray-color);
        }
    }

    .card-image {
        overflow: hidden;
