<template>
    <div class="position-fixed" id="loader" v-if="canShow">
        <div class="h-100 d-flex flex-column justify-content-center align-items-center">

            <p class="loading-txt" ref="loadingTxt">Loading Orbital</p>
            <div class="progress" ref="progress">
                <div class="progress-bar" role="progressbar" aria-label="range-loading" :aria-valuenow="percentLoaded"
                    aria-valuemin="0" aria-valuemax="100"></div>
            </div>

            <div class="spinner-parent text-center" v-if="!isError">
                <!-- <div class="spinner-border text-primary my-3" role="status">
                    <span class="visually-hidden">Loading...</span>
                </div> -->

                <p class="percentage" ref="percentage">{{ percentLoaded }}%</p>
            </div>

            <div class="error text-center" v-else>
                <h1 class="text-danger mb-2">Oups, somthing went wrong!</h1>
                <p class="lead mb-3">Please try again</p>
                <button class="custom-btn" @click="$emit('onRefresh')">Refresh</button>
            </div>
        </div>

    </div>
</template>


<script setup>

import { ref, watchEffect, computed } from "vue";
import { gsap } from "gsap";

defineEmits([
    "onRefresh"
]);

const props = defineProps({
    show: {
        type: Boolean,
        default: true
    },
    percentLoaded: {
        type: Number,
        default: 0
    },
    isError: {
        type: Boolean,
        default: false
    }
});

const progress = ref(null);
const loadingTxt = ref(null);
const percentage = ref(null);

const canShow = ref(true);
const animFinished = ref(false);

watchEffect(() => {
    if (!props.show && !animFinished.value) {
        _loaderAnimOut();
    }
});

const percentRangeLoading = computed(() => {
    return props.percentLoaded + "%";
});


function _loaderAnimOut() {
    gsap.to(progress.value, {
        width: 0,
        duration: 1.5,
        delay: .8,
        ease: "expo.inOut()",
        onComplete: () => {
            canShow.value = false;
            animFinished.value = true;
        }
    });

    gsap.to(loadingTxt.value, {
        y: 10,
        opacity: 0,
        duration: .7,
        delay: .5,
        ease: "expo.inOut()",
    });

    gsap.to(percentage.value, {
        y: -10,
        opacity: 0,
        duration: .7,
        delay: .5,
        ease: "expo.inOut()",
    });
}
</script>




<style lang="scss" scoped>
#loader {
    z-index: 1050;
    width: 100%;
    height: 100%;
    background-color: #ffffff;

    .progress {
        width: 30%;
        height: 0.1em;
        margin: 1em 0;

        .progress-bar {
            width: v-bind(percentRangeLoading);
            background-color: var(--app-primary-color);
        }
    }

    .loading-txt {
        color: var(--app-secondary-color);
        font-size: .8em;
        letter-spacing: .2em;
        text-transform: uppercase;
        margin: 1em 0;
    }

    .percentage {
        font-size: .9em;
        letter-spacing: .1em;
        color: var(--app-gray-color);
    }

}


.spinner-border {
    width: 5rem;
    height: 5rem;
}
</style>